import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import About from '@/pages/About'
import Authorization from '@/pages/Authorization'
import Case from '@/pages/Case'
// import ChampionCase from '@/pages/ChampionCase'
// import SecooCase from '@/pages/SecooCase'
import Contact from '@/pages/Contact'
import Join from '@/pages/Join'
import Services from '@/pages/Services'


Vue.use(Router)
/* 静态页面路由 */
const routes = [{
    path: '/',
    name: '首页',
    component: Home,
    meta: {
      title: '思空 | 首页' // 标题设置
    }
  },
  {
    path: '/about',
    name: '关于思空',
    component: About,
    meta: {
      title: '思空 | 关于思空' // 标题设置
    }
  },
  {
    path: '/authorization',
    name: 'IP授权',
    component: Authorization,
    meta: {
      title: '思空 | IP授权' // 标题设置
    }
  },
  {
    path: '/case',
    name: '案例介绍',
    component: Case,
    meta: {
      title: '思空 | 案例介绍' // 标题设置
    }
  },
  {
    path: '/contact',
    name: '联系我们',
    component: Contact,
    meta: {
      title: '思空 | 联系我们' // 标题设置
    }
  },
  {
    path: '/join',
    name: '加入我们',
    component: Join,
    meta: {
      title: '思空 | 加入我们' // 标题设置
    }
  },
  {
    path: '/services',
    name: '服务介绍',
    component: Services,
    meta: {
      title: '思空 | 服务介绍' // 标题设置
    }
  }
  // {
  //   path: '/championCase',
  //   name: '拳王争霸赛案例',
  //   component: ChampionCase,
  //   meta: {
  //     title: '思空 | 拳王争霸赛案例' // 标题设置
  //   }
  // },
  //   {
  //     path: '/secooCase',
  //     name: '寺库双十一系列营销案例',
  //     component: SecooCase,
  //     meta: {
  //       title: '思空 | 寺库双十一系列营销案例' // 标题设置
  //     }
  //   },
]

const router = new Router({
  routes
});

export default router



