<template>
  <div id="Case">
    <div id="all-area" class="all-section">
      <Header></Header>
      <section
        class="page-head page-bg page-case"
        style="background-image: url('./static/lib/image/newImg/skal.png')"
      ></section>
      <section class="page-in section page-carousel">
        <div class="row">
          <div class="col-xs-12 col-sm-12 col-md-12">
            <div id="myCarousel" class="carousel slide">
              <!-- 轮播（Carousel）指标 -->
              <ol class="carousel-indicators option">
                <li
                  data-target="#myCarousel"
                  :data-slide-to="index"
                  v-for="(item,index) in carousel"
                  :key="index"
                  @click="changeactive(index)"
                  :class="{'active':index==clickIndex}"
                  style="margin:0px 3px;"
                ></li>
              </ol>
              <!-- 轮播（Carousel）项目 -->
              <div class="carousel-inner">
                <div
                  class="item"
                  v-for="(item,index) in carousel"
                  :key="index"
                  :class="{'active':index==clickIndex}"
                  @click="changeactive(index)"
                >
                  <div class="Carousel-div" :class="item.classname">
                    <img :src="item.imgsrc" style="width:100%;">
                  </div>
                </div>
              </div>
              <a
                class="carousel-control left case-arrow case-arrow-left"
                href="#myCarousel"
                data-slide="prev"
              >
                <span>
                  <i class="fa fa-angle-left"></i>
                </span>
              </a>
              <a
                class="carousel-control right case-arrow case-arrow-right"
                href="#myCarousel"
                data-slide="next"
              >
                <span>
                  <i class="fa fa-angle-right"></i>
                </span>
              </a>
            </div>
          </div>
        </div>
      </section>
      <Footer></Footer>
    </div>
  </div>
</template>

<script>
import Header from "./../public/Header";
import Footer from "./../public/Footer";
export default {
  name: "Case",
  data() {
    return {
      clickIndex: 0,
      carousel: [
        {
          classname: "case1",
          imgsrc: "./static/lib/image/case/case1.png"
        },
        {
          classname: "case2",
          imgsrc: "./static/lib/image/case/case2.png"
        },
        {
          classname: "case3",
          imgsrc: "./static/lib/image/case/case3.png"
        }
      ]
    };
  },
  components: {
    Header,
    Footer
  },
  mounted() {
    this.getclickIndex();
  },
  methods: {
    getclickIndex() {
      if (
        this.$route.query.id == "" ||
        this.$route.query.id == null ||
        this.$route.query.id == undefined
      ) {
        this.clickIndex = 0;
      } else {
        this.clickIndex = this.$route.query.id;
      }
    },

    changeactive(index) {
      this.clickIndex = index;
    }
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



