<template>
  <div class="footer">
    <!-- Footer AREA START -->
    <footer class="footer">
      <div class="footer-main cbiz-bg section">
        <div class="container">
          <div class="row">
            <div class="widgets">
              <!-- /.col- -->
              <div class="col-xs-12 col-sm-12 col-md-11 col-md-offset-2">
                <div class="widget">
                  <div class="wi-content wi-news col-md-7">
                    <ul>
                      <li>
                        <div class="news-left">
                          <a href="javascript:void(0)">
                            <img :src="logoimg" alt="logo">
                          </a>
                        </div>
                        <div class="news-right">
                          <p class="news-title">{{companyname}}</p>
                          <div class="posted">
                            <span class="post-date">
                              <a href="javascript:void(0)">
                                <i class="fa fa-map-marker"></i>
                                {{companyaddress}}
                              </a>
                            </span>
                            <br>
                            <span class="post-tag">
                              <a href="javascript:void(0)">
                                <i class="fa fa-phone"></i>
                                {{companyphone}}
                              </a>
                            </span>
                          </div>
                        </div>
                        <!-- /.news-right -->
                      </li>
                    </ul>
                  </div>
                  <div class="wi-content wi-news col-md-5 col-md-pull-1">
                    <div class="contact-social">
                      <ul>
                        <li v-for="(item,index) in weixincontact" :key="index">
                          <a href="javascript:void(0)">
                            <img :src="item.img">
                            <p>{{item.title}}</p>
                          </a>
                        </li>
                      </ul>
                    </div>
                  </div>
                  <!-- /wi-content -->
                </div>
                <!-- /.widget -->
              </div>
              <!-- /.col- -->
            </div>
            <!-- /.widgets -->
          </div>
          <!-- /.row -->
        </div>
        <!-- /.container -->
      </div>
      <!-- /.footer-main -->
      <div class="copyright">
        <div class="container">
          <a href="javascript:void(0)" title="思空官网备案" target="_blank">{{companyrecord}}</a>
          {{record}}
        </div>
      </div>
      <!-- /.copyright -->
    </footer>
    <!-- /.footer -->
    <!-- /Footer AREA END -->
    <!-- Go To Top Link -->
    <a href="#" class="back-to-top">
      <i class="fa fa-angle-up"></i>
    </a>
    <img
      src="./../../static/lib/image/ls/suspensionBoxshow.png"
      class="fix-suspensionshow animated bounceInLeft"
      v-if="isshow"
      @click="isshowfix_suspension()"
    >
    <img
      src="./../../static/lib/image/ls/suspensionBox.png"
      class="fix-suspension animated flipInY"
      v-if="show"
      @click="showfix_suspension()"
    >
  </div>
</template>

<script>
export default {
  name: "Footer",
  data() {
    return {
      show: false,
      isshow: true,
      logoimg: "./static/lib/image/ls/footer_logo.png",
      companyname: "深圳前海思空文化传播有限公司",
      companyaddress: "深圳市南山区粤海街道科兴科学园A1栋19层",
      companyphone: "0755-26605587",
      companyrecord: "粤ICP备18107418号-2",
      record: "深圳前海思空文化传播有限公司版权所有",
      weixincontact: [
        { img: "./static/lib/image/1.png", title: "签约联系" },
        { img: "./static/lib/image/2.png", title: "商务联系" }
      ]
    };
  },
  methods: {
    isshowfix_suspension() {
      this.show = true;
      this.isshow = false;
    },
    showfix_suspension() {
      this.show = false;
      this.isshow = true;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



