<template>
  <div id="Authorization">
    <div id="all-area" class="all-section">
      <Header></Header>
      <section
        class="page-head page-bg page-bg-1"
        style="background-image: url('./static/lib/image/ls/ip-1.png')"
      ></section>
      <section id="main-slider">
        <div id="main-slide" class="main-slider carousel slide" data-ride="carousel">
          <!-- data-ride="carousel" -->
          <div class="carousel-inner">
            <div
              v-for="(item,index) in indexslider"
              :key="index"
              class="item"
              :class="{'active':index==clickIndex}"
            >
              <img class="img-responsive" :src="item.src" alt="slider">
            </div>
            <a class="left carousel-control" href="#main-slide" data-slide="prev">
              <span>
                <i class="fa fa-angle-left"></i>
              </span>
            </a>
            <a class="right carousel-control" href="#main-slide" data-slide="next">
              <span>
                <i class="fa fa-angle-right"></i>
              </span>
            </a>
            <div class="click-to-next bottom-top-animation">
              <i></i>
            </div>
          </div>
        </div>
      </section>
      <section
        class="page-head page-bg page-bg-common"
        style="background-image: url('./static/lib/image/ls/ip-2.png')"
      ></section>
      <section class="page-nndw-introduce" style>
        <div class>
          <div class="row">
            <div class="col-md-12 introduce-title">NIUNIU&DUANWU</div>
            <div class="col-md-12 introduce-title-bottom"></div>
            <div class="col-md-5 introduce-text">
              <ul class="introduce-text-ul">
                <li class="introduce-text-li">
                  <span class="introduce-text-label">昵称：</span>妞胖、妞婊、羊、海豹
                </li>
                <li class="introduce-text-li">
                  <span class="introduce-text-label">性格：</span>甜美单纯、温和善良、偶尔有点小怂包
                </li>
                <li class="introduce-text-li">
                  <span class="introduce-text-label">特征：</span>天生丽质，喜欢打扮，招牌是治愈系的甜美笑容，重视友情，可惜抵挡不住美食诱惑，体重指数堪忧
                </li>
              </ul>
            </div>
            <div class="col-md-7 introduce-img">
              <ul class="introduce-img-ul">
                <li class="introduce-img-li">
                  <img src="../../static/lib/image/ls/nn1.png">
                </li>
                <li class="introduce-img-li">
                  <img src="../../static/lib/image/ls/nn2.png">
                </li>
                <li class="introduce-img-li">
                  <img src="../../static/lib/image/ls/nn3.png">
                </li>
                <li class="introduce-img-li">
                  <img src="../../static/lib/image/ls/nn4.png">
                </li>
              </ul>
              <div class="col-md-12 introduce-img-name">小公主萨摩耶 - 妞妞</div>
            </div>
          </div>
        </div>
      </section>
      <section
        class="page-border"
        style="background-image: url('./static/lib/image/ls/border.png')"
      ></section>
      <section class="page-nndw-dw-introduce" style>
        <div class>
          <div class="row">
            <div class="col-md-7 introduce-dw-img">
              <ul class="introduce-dw-img-ul">
                <li class="introduce-dw-img-li">
                  <img src="../../static/lib/image/ls/dw1.png">
                </li>
                <li class="introduce-dw-img-li">
                  <img src="../../static/lib/image/ls/dw2.png">
                </li>
                <li class="introduce-dw-img-li">
                  <img src="../../static/lib/image/ls/dw3.png">
                </li>
                <li class="introduce-dw-img-li">
                  <img src="../../static/lib/image/ls/dw4.png">
                </li>
              </ul>
              <div class="col-md-12 introduce-dw-img-name">傲娇女王苏格兰折耳猫 - 端午</div>
            </div>
            <div class="col-md-4 introduce-dw-text">
              <ul class="introduce-dw-text-ul">
                <li class="introduce-text-li" style="font-size:16px;color: #424242;">
                  <span class="introduce-text-label">昵称：</span>端胖、端牛、猕猴桃、海参
                </li>
                <li class="introduce-text-li" style="font-size:16px;color: #424242;">
                  <span class="introduce-text-label">性格：</span>极其高冷，拒不卖萌；艺高猫胆大，一本正经地搞笑，反差萌；刀子嘴豆腐心；
                </li>
                <li class="introduce-text-li" style="font-size:16px;color: #424242;">
                  <span class="introduce-text-label">特征：</span>绿眼睛，坐铲屎官脸上，喜欢赏花、看鸟看鱼、看电视，退休老干部，睡觉喜欢打呼，经常选择性失聪
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
      <section
        class="page-head page-bg page-bg-common"
        style="background-image: url('./static/lib/image/ls/ip-3.png')"
      ></section>
      <section class="page-head page-GrowUp" style>
        <img src="../../static/lib/image/ls/ip-2-2.png" class="page-GrowUp-img">
      </section>
      <section
        class="page-head page-bg page-bg-common"
        style="background-image: url('./static/lib/image/ls/ip-4.png')"
      ></section>
      <Publicimg></Publicimg>
      <Footer></Footer>
    </div>
  </div>
</template>

<script>
import Header from "./../public/Header";
import Publicimg from "./../public/Publicimg";
import Footer from "./../public/Footer";
export default {
  name: "Authorization",
  data() {
    return {
      //轮播数据
      clickIndex: "0",
      indexslider: [
        {
          src: "./static/lib/image/ls/lingfans.png"
        },
        {
          src: "./static/lib/image/ls/banner-food.png"
        },
        {
          src: "./static/lib/image/ls/banner1-2.png"
        },
        {
          src: "./static/lib/image/ls/banner-seafood.png"
        }
      ]
    };
  },
  components: {
    Header,
    Publicimg,
    Footer
  },
  methods: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



