<template>
  <div id="all-area" class="all-section">
    <div id="header">
      <div id="loader">
        <div class="spinner">
          <div class="dot1"></div>
          <div class="dot2"></div>
        </div>
      </div>
      <!-- Header Area START -->
      <header class="clearfix header" data-spy="affix" data-offset-top="60">
        <!-- Start  Logo & Menu  -->
        <div class="navbar navbar-default navbar-top">
          <div class="container">
            <div class="navbar-header">
              <!-- Stat Toggle Nav Link For Mobiles -->
              <button
                type="button"
                class="navbar-toggle"
                data-toggle="collapse"
                data-target=".navbar-collapse"
              >
                <i class="fa fa-bars"></i>
              </button>
              <!-- End Toggle Nav Link For Mobiles -->
              <div class="logo">
                <a class="navbar-brand" @click="routerindex()">
                  <img alt="Logo" src="./../../static/lib/image/ls/logo.png">
                </a>
              </div>
              <!-- /.logo -->
            </div>
            <div class="navbar-collapse collapse">
              <!-- Start Navigation List -->
              <ul class="nav navbar-nav navbar-right">
                <li v-for="(item,index) in navmenu" :key="index">
                  <a
                    :class="{active:navchangeIndex == index}"
                    @click="navchange(index)"
                  >{{item.name}}</a>
                  <ul class="dropdown">
                    <li
                      v-for="(childitem,index) in item.childname"
                      :key="index"
                      @click="childnavchange(index)"
                    >
                      <a>{{childitem.name}}</a>
                    </li>
                  </ul>
                </li>
              </ul>
              <!-- End Navigation List -->
            </div>
            <!-- /.navbar-collapse -->
          </div>
          <!-- /.container -->

          <!-- Mobile Menu Start -->
          <ul class="wpb-mobile-menu">
            <li @click="navchange(0)">
              <a class="active">首页</a>
            </li>
            <li @click="navchange(1)">
              <a>关于思空</a>
            </li>
            <li @click="navchange(2)">
              <a>服务介绍</a>
            </li>
            <li @click="navchange(3)">
              <a>案例介绍</a>
              <ul class="dropdown">
                <li @click="childnavchange(0)">
                  <a>闪耀暖暖</a>
                </li>
                <li @click="childnavchange(1)">
                  <a>卡姿兰</a>
                </li>
                <li @click="childnavchange(2)">
                  <a>植观</a>
                </li>
              </ul>
            </li>
            <li @click="navchange(4)">
              <a>IP授权</a>
            </li>
            <li @click="navchange(5)">
              <a>加入我们</a>
            </li>
            <li @click="navchange(6)">
              <a>联系我们</a>
            </li>
          </ul>
          <!-- /.wpb-mobile-menu -->
        </div>
        <!-- /.navbar -->
        <!-- End Header Logo & Naviagtion -->
      </header>
      <!-- /.header -->
      <!--Header Area END -->
    </div>
  </div>
</template>

<script>
export default {
  name: "Header",

  data() {
    return {
      navchangeIndex: 0,
      navmenu: [
        { name: "首页" },
        { name: "关于思空" },
        { name: "服务介绍" },
        {
          name: "案例介绍",
          childname: [
            { name: "闪耀暖暖" },
            { name: "卡姿兰" },
            { name: "植观" }
          ]
        },
        { name: "IP授权" },
        { name: "加入我们" },
        { name: "联系我们" }
      ]
    };
  },

  mounted: function() {
    this.getnavnum();
  },
  methods: {
    getnavnum() {
      if (
        window.sessionStorage.getItem("navnum") == "" ||
        window.sessionStorage.getItem("navnum") == null ||
        window.sessionStorage.getItem("navnum") == undefined
      ) {
        this.navchangeIndex = 0;
      } else {
        this.navchangeIndex = sessionStorage.getItem("navnum");
      }
    },
    setnavnum(index) {
      const str = index;
      sessionStorage.setItem("navnum", str);
    },
    navchange(index) {
      this.setnavnum(index);
      if (index == 0) {
        this.$router.push("/");
        location.reload();
      } else if (index == 1) {
        this.$router.push("/about");
        location.reload();
      } else if (index == 2) {
        this.$router.push("/services");
        location.reload();
      } else if (index == 3) {
      } else if (index == 4) {
        this.$router.push("/authorization");
        location.reload();
      } else if (index == 5) {
        this.$router.push("/join");
        location.reload();
      } else if (index == 6) {
        this.$router.push("/contact");
        location.reload();
      }
    },
    childnavchange(index) {
      this.setnavnum(3);
      this.$router.push({
        path: "/case",
        query: {
          id: index
        }
      });
      location.reload();
    },
    routerindex() {
      this.$router.push("/");
      sessionStorage.setItem("navnum", 0);
      location.reload();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  cursor: pointer;
}
</style>



