<template>
  <div>
    <section class="page-head page-friend" v-if="this.services==2">
      <img :src="servicesimg" class="kol-friend">
    </section>
    <section class="page-head page-cooperation" v-if="this.services==4">
      <img :src="authorizationimg" class="page-cooperation-img">
    </section>
  </div>
</template>
<script>
export default {
  name: "Publicimg",
  data() {
    return {
      services: "",
      servicesimg: "./static/lib/image/services/friend.jpg",
      authorizationimg: "./static/lib/image/ls/ip-cooperation.png"
    };
  },
  mounted() {
    this.getItem();
  },
  methods: {
    getItem() {
      this.services = sessionStorage.getItem("navnum");
    }
  }
};
</script>

<style>
.page-friend {
  margin: 0 auto;
  width: 100%;
  padding-bottom: 0px;
}
</style>



