<template>
  <div id="Contact">
    <div id="all-area" class="all-section">
      <Header></Header>
      <section
        class="page-head page-bg page-contact"
        style="background-image: url('./static/lib/image/contact/contact-bg.jpg')"
        v-if="index==0"
      >
        <div class="container">
          <div class="row header-contact">
            <div @click.stop="changes(1)">
              <img src="./../../static/lib/image/contact/contactInfoBg.png">
            </div>
          </div>
        </div>
      </section>
      <section
        class="page-head page-bg page-contact"
        style="background-image: url('./static/lib/image/contact/contact-bg.jpg')"
        @dblclick="change(0)"
        v-if="index==1"
      >
        <div class="container">
          <div class="row header-contact">
            <div class="amap-wrapper">
              <el-amap
                ref="map"
                vid="amapDemo"
                :amap-manager="amapManager"
                :center="center"
                :zoom="zoom"
                :plugin="plugin"
                :events="events"
                class="amap-demo"
              >
                <el-amap-info-window :position="center" :content="address"></el-amap-info-window>
              </el-amap>
            </div>
          </div>
        </div>
      </section>
      <Footer></Footer>
    </div>
  </div>
</template>

<script>
import Header from "./../public/Header";
import Footer from "./../public/Footer";
import { amapManager } from "vue-amap";
export default {
  name: "Contact",
  data() {
    return {
      index: 0,
      address: "深圳市南山区粤海街道科兴科学园A1栋19层",
      zoom: 12,
      center: [113.942321, 22.548662],
      amapManager,
      events: {
        init(o) {
          let marker = new AMap.Marker({
            position: [113.942321, 22.548662]
          });
          marker.setMap(o);
        }
      },
      moveend: () => {},
      zoomchange: () => {},
      plugin: [
        "ToolBar",
        {
          pName: "MapType",
          defaultType: 0
        }
      ]
    };
  },
  components: {
    Header,
    Footer
  },
  methods: {
    change(ins) {
      this.index = ins;
    },
    changes(inss) {
      this.index = inss;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.amap-wrapper {
  color: #2e8df9;
  width: 80vw;
  height: 50vh;
  margin: 0px auto;
  box-shadow: 0 2px 8px rgba(249, 186, 72, 0.5),
    0 0 20px rgba(249, 186, 72, 0.9);
}
@media (min-width: 1441px) {
  .amap-wrapper {
    color: #2e8df9;
    width: 45vw;
    height: 40vh;
    margin: 0px auto;
  }
}
</style>



