<template>
  <div>
    <div class="col-xs-12 col-sm-6">
      <!-- /.contact-content -->
      <div class="lets-contact">
        <ul class="contact-list recruit-list">
          <li>
            <a href="#" class="contact-tag">
              <i class="fa fa-envelope"></i>
              {{emailname}}
            </a>
            <p>{{email}}</p>
          </li>
          <li>
            <a href="#" class="contact-tag">
              <i class="fa fa-th-large"></i>
              {{zhaopin}}
            </a>
            <p>{{zhaopinurl}}</p>
          </li>
          <li>
            <a href="#" class="contact-tag">
              <i class="fa fa-phone"></i>
              {{zhaopinphone}}
            </a>
            <p>{{zhaopinphonenum}}</p>
          </li>
          <li class="tag1">
            <a
              class="tag"
              v-for="(item,index) in taglist"
              :key="index"
              style="margin:2px;"
            >{{item.tag}}</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="col-xs-12 col-sm-6">
      <ul class="contact-list recruit-list">
        <li v-for="(item,index) in textlist" :key="index">
          <img :src="item.src" class="good-img">
          <span class="good-text">{{item.content}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      emailname: "招聘邮箱：",
      email: "tk-hr@thinkoonjt.com",
      zhaopin: "拉勾网招聘：",
      zhaopinurl: "https://www.lagou.com/gongsi/537416.html",
      zhaopinphone: "招聘电话：",
      zhaopinphonenum: "0755-26605585",
      taglist: [
        { tag: "朝十晚七" },
        { tag: "双休" },
        { tag: "下午茶" },
        { tag: "带薪年假" },
        { tag: "年终奖" }
      ],
      textlist: [
        {
          src: "./static/lib/image/contact/good.png",
          content: "工作时间弹性制，十点上班、双休、国家法定节假日;"
        },
        {
          src: "./static/lib/image/contact/good.png",
          content: "工作环境舒适，公司位于高大上的科兴科学园，跟腾讯做邻居;"
        },
        {
          src: "./static/lib/image/contact/good.png",
          content:
            "工作氛围轻松，把兴趣当工作，跟一群有趣、有梦想、三观端正的年轻人一起共事，不为名利做毁三观的事情，拒绝为了达到目的不择手段的人;"
        },
        {
          src: "./static/lib/image/contact/good.png",
          content:
            "专业靠谱的团队，科学系统的职业培训，总量数亿的微博流量，与百万粉段子手、大V当同事，手把手教你玩转微博;"
        },
        {
          src: "./static/lib/image/contact/good.png",
          content:
            "良好的职业发展空间，具有竞争的职位晋升机制，丰厚的项目奖金与提成，你的能力与回报挂钩，我们不会亏待任何一位人才;"
        },
        {
          src: "./static/lib/image/contact/good.png",
          content: "新媒体行业内领先的知名企业，思空文化传播因为有你而强大!"
        }
      ]
    };
  }
};
</script>




