                "2、诚实守信，爱岗敬业，责任心强，具有良好的协调沟通能力、分析能力和判断能力，善于思考和解决问题；"
            },
            {
              name:
                "3、对数据有一定的敏感度，谨慎细致的性格，能保证数据的准确率优先。"
            }
          ]
        }
      ]
    };
  },
  components: {
    Header,
    Recruitment,
    Footer
  },
  methods: {
    changeactive(num) {
      this.ins = num;
    }
  }
};
</script>

